//import p5 from 'p5'

class Node {
  constructor(myP5, i, j, w, h, rows, cols, type) {
    this.p = myP5
    this.i = i
    this.j = j
    this.w = w
    this.h = h
    this.rows = rows
    this.cols = cols

    this.ff = 0
    this.fg = 0
    this.fh = 0
    this.neighbours = []
    this.parent = undefined

    this.type = type
  }
  show(color) {
    this.p.fill(color)
    //this.p.noStroke()
    //this.p.stroke(0)
    //this.p.strokeWeight(0.6)
    this.p.strokeWeight(0.3)
    this.p.stroke('rgba(37,188,255, 1)')
    this.p.rectMode(this.p.CORNER)
    this.p.ellipseMode(this.p.CENTER)
    this.p.rect(this.i * this.w, this.j * this.h, this.w, this.h)

    //this.p.strokeWeight(1)
    if (this.type === 'S') {
      //this.p.rect(this.i * this.w, this.j * this.h, this.w, this.h)
      // this.p.ellipse(
      //   this.i * this.w + this.w / 2,
      //   this.j * this.h + this.h / 2,
      //   this.w / 2
      // )

      this.p.stroke('rgba(255, 0, 255, 1)')
      this.p.fill('rgba(255, 0, 255, 0.2)')
      this.p.push()
      this.p.rectMode(this.p.CENTER)
      this.p.rect(
        this.i * this.w + this.w / 2,
        this.j * this.h + this.h / 2,
        this.w / 2,
        this.h / 2
      )
      this.p.pop()
    } else if (this.type === 'E') {
      //this.p.rect(this.i * this.w, this.j * this.h, this.w, this.h)

      this.p.stroke('rgba(255, 0, 255, 1)')
      this.p.fill('rgba(255, 0, 255, 0.2)')
      //this.p.stroke(255, 0, 255)
      this.p.ellipse(
        this.i * this.w + this.w / 2,
        this.j * this.h + this.h / 2,
        this.w
      )
      //this.p.strokeWeight(2)
      this.p.ellipse(
        this.i * this.w + this.w / 2,
        this.j * this.h + this.h / 2,
        this.w / 5
      )
    } else {
      this.p.rect(this.i * this.w, this.j * this.h, this.w, this.h)
    }
  }
  addNeighbours(grid) {
    let i = this.i
    let j = this.j
    if (i < this.cols - 1) {
      this.neighbours.push(grid[i + 1][j])
    }
    if (i > 0) {
      this.neighbours.push(grid[i - 1][j])
    }
    if (j < this.rows - 1) {
      this.neighbours.push(grid[i][j + 1])
    }
    if (j > 0) {
      this.neighbours.push(grid[i][j - 1])
    }
  }
}

export default Node
