import React, { Component } from 'react'
import p5 from 'p5'

import SortingSideBar from '../SortingSideBar'
import SortConfig from '../SortConfig'

class BubbleSort extends Component {
  constructor() {
    super()
    this.myP5 = null
    this.myRef = React.createRef()
    this.arr = []
    this.normArr = []

    this.sketch = (p) => {
      p.setup = () => {
        p.pixelDensity(1)
        p.createCanvas(1000, 350).parent(this.myRef.current)
        p.noLoop()
      }
      p.draw = () => {
        p.background(255)

        let barWidth = p.width / this.normArr.length - 1
        console.log(barWidth)
        for (let i = 0; i < this.normArr.length; i++) {
          let x0 = i * (p.width / this.normArr.length)
          let y0 = p.height
          let barHeight = -this.normArr[i]

          p.noStroke()
          p.fill('rgba(37,188,255, 1)')
          p.rect(x0, y0, barWidth, barHeight, 0, 0, 2, 2)

          if (this.arr.length <= 60) {
            p.fill(255)
            p.textSize(barWidth / 2)
            p.textAlign(p.CENTER, p.TOP)
            p.text(this.arr[i], x0 + barWidth / 2, y0 + barHeight + 10)
          }
        }

        //console.log('onee')
      }

      p.initializeRandomValue = (size, min, max) => {
        this.arr = []
        for (let i = 0; i < size; i++) {
          this.arr.push(p.floor(p.random(min, max + 1)))
        }
        console.log(this.arr)
        this.normArr = this.arr.map((x) =>
          p.floor(p.map(x, min, max, 5, 330, true))
        )
        console.log(this.normArr)
        p.redraw()
      }
    }
  }

  handleRandom = (size, min, max) => {
    this.myP5.initializeRandomValue(size, min, max)
  }
  componentDidMount() {
    //this.setState({ isRandom: true })
    this.myP5 = new p5(this.sketch)
  }

  render() {
    return (
      <div className="row">
        <div className="col-2 side-bar">
          <SortingSideBar />
        </div>
        <div className="col-10 main-content">
          <SortConfig title="Bubble Sort" initRandom={this.handleRandom} />
          <div className="row">
            <div className="col" ref={this.myRef}></div>
          </div>
        </div>
      </div>
    )
  }
}

export default BubbleSort
