import React, { Component } from 'react'
import p5 from 'p5'

import SearchSideaBar from '../SearchSideBar'
import SearchConfig from '../SearchConfig'
import Node from './Node'

class AStarSearch extends Component {
  constructor() {
    super()

    //console.log('constructor')
    this.myP5 = null
    this.canvasRef = React.createRef()

    this.sketch = (p) => {
      const cols = 37
      const rows = 15
      //const CELL_SIZE = 10

      let grid = []
      let w = 0
      let h = 0

      let openSet = []
      let closedSet = []
      let start = null
      let end = null
      let path = []
      let depthAlpha = 0

      let palyButton = false
      p.setup = () => {
        p.initializeSetUp()
      }

      p.initializeSetUp = () => {
        p.loop()
        grid = []
        openSet = []
        closedSet = []
        path = []
        palyButton = false
        depthAlpha = 0.2

        p.frameRate(50)
        p.createCanvas(1050, 400).parent(this.canvasRef.current)

        w = p.width / cols
        h = p.height / rows
        // Create a 2D array
        for (let i = 0; i < cols; i++) {
          grid[i] = new Array(rows)
        }

        // initialize all the Node
        for (let i = 0; i < cols; i++) {
          for (let j = 0; j < rows; j++) {
            grid[i][j] = new Node(p, i, j, w, h, rows, cols, '_')
          }
        }
        // add neighbours
        for (let i = 0; i < cols; i++) {
          for (let j = 0; j < rows; j++) {
            grid[i][j].addNeighbours(grid)
          }
        }

        start = grid[p.floor(3)][p.floor(rows / 2)]
        end = grid[cols - 3][p.floor(rows / 2)]

        // start = grid[0][0]
        // end = grid[cols - 1][rows - 1]

        start.type = 'S'
        end.type = 'E'

        openSet.push(start)

        this.drawMaze()
        //console.log(grid)
        //console.log(closedSet)
      }

      this.drawMaze = () => {
        p.background(255)

        for (let i = 0; i < cols; i++) {
          for (let j = 0; j < rows; j++) {
            grid[i][j].show(p.color(255, 255, 255))
          }
        }

        for (let i = 0; i < closedSet.length; i++) {
          //closedSet[i].show(p.color(255, 0, 0))
          closedSet[i].show(p.color(255, 0, 255, 255 * depthAlpha))
          depthAlpha += 0.05
        }
        for (let i = 0; i < openSet.length; i++) {
          openSet[i].show(p.color(0, 255, 0))
        }
        for (let i = 0; i < path.length; i++) {
          path[i].show(p.color(0, 0, 255))
        }

        start.show(p.color(255, 255, 255))
        end.show(p.color(255, 255, 255))
      }
      p.setPlay = () => {
        palyButton = true
      }
      p.setPause = () => {
        palyButton = false
      }
      p.setSetpForward = () => {
        palyButton = true
        p.redraw()
        palyButton = false
      }
      p.setAnimationSpeed = (fps) => {
        p.frameRate(parseInt(fps))
        console.log(fps)
      }

      p.draw = () => {
        //console.log('playButton value:' + palyButton)
        if (palyButton === true) {
          if (openSet.length > 0) {
            let winner = 0
            for (let i = 0; i < openSet.length; i++) {
              if (openSet[i].ff < openSet[winner].ff) {
                winner = i
              }
            }

            let current = openSet[winner]
            if (current === end) {
              path = []
              let temp = current
              path.push(temp)
              while (temp.parent) {
                path.push(temp.parent)
                temp = temp.parent
              }
              console.log('DONE!!')
              p.noLoop()
            }

            for (let i = openSet.length; i >= 0; i--) {
              if (openSet[i] === current) {
                openSet.splice(i, 1)
              }
            }
            // if (!closedSet.includes(current)) {
            // }
            closedSet.push(current)
            let neighbours = current.neighbours
            for (let i = 0; i < neighbours.length; i++) {
              let neighbour = neighbours[i]

              if (!closedSet.includes(neighbour)) {
                let tempG = current.fg + 1

                if (openSet.includes(neighbour)) {
                  if (tempG < neighbour.fg) {
                    neighbour.fg = tempG
                  }
                } else {
                  neighbour.fg = tempG
                  openSet.push(neighbour)
                }

                neighbour.fh = p.dist(neighbour.i, neighbour.j, end.i, end.j)
                // neighbour.fh =
                //   p.abs(neighbour.i - end.i) * p.abs(neighbour.j - end.j)

                neighbour.ff = neighbour.fg + neighbour.fh
                neighbour.parent = current
              }
            }
            //keep going
          } else {
            //no solution
          }
          this.drawMaze()
          //end of draw
        }
      }
    }
  }

  initializeMaze = () => {
    this.myP5.initializeSetUp()
  }
  setPlay = () => {
    this.myP5.setPlay()
  }
  setPause = () => {
    this.myP5.setPause()
  }
  setSetpForward = () => {
    this.myP5.setSetpForward()
  }
  setAnimationSpeed = (fps) => {
    this.myP5.setAnimationSpeed(fps)
  }

  componentDidMount() {
    console.log('mount')
    this.myP5 = new p5(this.sketch)
  }
  render() {
    return (
      <div className="row">
        <div className="col-2 side-bar">
          <SearchSideaBar />
        </div>
        <div className="col-10 main-content">
          <SearchConfig
            title="A* Search"
            initializeMaze={this.initializeMaze}
            playButton={this.setPlay}
            pauseButton={this.setPause}
            setpForwardButton={this.setSetpForward}
            setAnimationSpeed={this.setAnimationSpeed}
          />
          <div className="row mt-3">
            <div className="col" ref={this.canvasRef}></div>
          </div>
          {/* <div className="col mb-5" ref={this.canvasRef}></div> */}
        </div>
      </div>
    )
  }
}
export default AStarSearch
