import React, { Component } from 'react'
import 'react-bootstrap-range-slider/dist/react-bootstrap-range-slider.css'
import RangeSlider from 'react-bootstrap-range-slider'

class SearchConfig extends Component {
  constructor() {
    super()
    this.state = {
      animationSpeed: 30
    }
  }
  render() {
    return (
      <fieldset className="fieldset-container">
        <legend>
          <span className="legend-border"> ===[ </span> {this.props.title}
          <span className="legend-border"> ]=== </span>
        </legend>

        <div className="input-group mb-3">
          <div className="input-group-prepend">
            <label className="input-group-text" htmlFor="inputGroupSelect01">
              Maze Type
            </label>
          </div>
          <select className="custom-select mr-4 " id="inputGroupSelect01">
            <option value="0">Empty Maze</option>
            <option value="1">One</option>
            <option value="2">Two</option>
            <option value="3">Three</option>
          </select>

          <div className="input-group-prepend">
            <label className="input-group-text" htmlFor="inputGroupSelect02">
              Animation Speed
            </label>
          </div>
          <div className="mr-4 ml-2" id="inputGroupSelect02">
            <RangeSlider
              data-tip="Animation Speed"
              value={this.state.animationSpeed}
              min={1}
              max={60}
              tooltip="off"
              // labels={{ 0: 'Low', 100: 'High' }}
              onChange={(e) => {
                this.setState({ animationSpeed: parseInt(e.target.value) })
                this.props.setAnimationSpeed(e.target.value)
                //console.log(this.state.animationSpeed)
              }}
            />
          </div>

          <div className="mr-4">
            <button
              type="button"
              className="btn btn-primary"
              onClick={() => this.props.playButton()}
            >
              Play
            </button>
          </div>
          <div className="mr-4">
            <button
              type="button"
              className="btn btn-primary"
              onClick={() => this.props.pauseButton()}
            >
              Pause
            </button>
          </div>

          <div className="mr-4">
            <button
              type="button"
              className="btn btn-primary"
              onClick={() => this.props.setpForwardButton()}
            >
              Step Forward
            </button>
          </div>

          <div className="mr-4">
            <button
              type="button"
              className="btn btn-primary"
              onClick={() => this.props.initializeMaze()}
            >
              Clear board
            </button>
          </div>
        </div>
      </fieldset>
    )
  }
}

export default SearchConfig
