import React, { Component } from 'react'
import BootstrapSwitchButton from 'bootstrap-switch-button-react'
import 'react-bootstrap-range-slider/dist/react-bootstrap-range-slider.css'
import RangeSlider from 'react-bootstrap-range-slider'

class SortConfig extends Component {
  constructor() {
    super()
    this.state = {
      inputSize: 50,
      minValue: 3,
      maxValue: 100,
      isRandom: true,
      animationSpeed: 1
    }
  }
  handleChecked = () => {
    this.setState({ isRandom: !this.state.isRandom }, () => {})
  }
  handleInputSize = (e) => {
    this.setState({ inputSize: parseInt(e.target.value) }, () => {})
  }
  handleMinValue = (e) => {
    this.setState({ minValue: parseInt(e.target.value) }, () => {})
  }
  handleMaxValue = (e) => {
    this.setState({ maxValue: parseInt(e.target.value) }, () => {})
  }
  render() {
    return (
      <fieldset className="fieldset-container">
        <legend>
          <span className="legend-border"> ===[ </span> {this.props.title}
          <span className="legend-border"> ]=== </span>
        </legend>

        <div className="row align-items-center">
          <div className="col-8">
            <div className="row">
              <div className="col-12">
                <BootstrapSwitchButton
                  size="sm"
                  checked={this.state.isRandom}
                  onChange={this.handleChecked}
                  onlabel="Input type: Randomized"
                  offlabel="Input type: Manual"
                  onstyle="success"
                  offstyle="danger"
                  width={430}
                  // height={25}
                />
              </div>
            </div>

            <div className="row">
              {this.state.isRandom && (
                <div className="col-12">
                  <div className="row m-1 align-items-center">
                    <div className="col-1">Size:</div>
                    <div className="col-2">
                      <input
                        className="form-control form-control-sm"
                        type="number"
                        value={this.state.inputSize}
                        onChange={this.handleInputSize}
                        min={3}
                        max={100}
                      />
                    </div>
                    <div className="col-1">Min:</div>
                    <div className="col-2">
                      <input
                        type="number"
                        className="form-control form-control-sm"
                        value={this.state.minValue}
                        onChange={this.handleMinValue}
                      />
                    </div>
                    <div className="col-1">Max:</div>
                    <div className="col-2">
                      <input
                        type="number"
                        className="form-control form-control-sm"
                        value={this.state.maxValue}
                        onChange={this.handleMaxValue}
                      />
                    </div>
                    <div className="col">
                      <button
                        type="button"
                        className="btn-sm btn-secondary"
                        onClick={() =>
                          this.props.initRandom(
                            this.state.inputSize,
                            this.state.minValue,
                            this.state.maxValue
                          )
                        }
                      >
                        Initialize Array
                      </button>
                    </div>
                  </div>
                </div>
              )}
              {!this.state.isRandom && (
                <div className="col-12">
                  <div className="row m-1 align-items-center">
                    <div className="col-3">Enter Values:</div>
                    <div className="col-6">
                      <input
                        className="form-control form-control-sm"
                        type="text"
                        placeholder="Comma seperated values"
                      />
                    </div>
                    <div className="col-3 text-right">
                      <button
                        type="button"
                        className="btn-sm btn-secondary"
                        onClick={this.handleDataGeneration}
                      >
                        Initialize Array
                      </button>
                    </div>
                  </div>
                </div>
              )}

              <div className="col-5"></div>
            </div>
          </div>

          <div className="col-2">
            <span>Animation Speed</span>
            <RangeSlider
              data-tip="Animation Speed"
              value={this.state.animationSpeed}
              min={1}
              max={100}
              tooltip="off"
              labels={{ 0: 'Low', 100: 'High' }}
              onChange={(e) => {
                this.setState({ animationSpeed: parseInt(e.target.value) })
                console.log(this.state.animationSpeed)
              }}
            />
          </div>
          <div className="col-2">
            <button type="button" className="btn btn-secondary">
              {this.props.title}
            </button>
          </div>
        </div>
      </fieldset>
    )
  }
}

export default SortConfig
